import React, { Fragment } from 'react'

const NotFound = () => (
  <Fragment>
    <h1 className="display-4">
      <span className="text-primary">404</span> Page Not Found
    </h1>
    <p className="lead">
      Sorry, that page does not exists
    </p>
  </Fragment>
)

export default NotFound



